.box {
    border: 1px solid rgb(180, 160, 166);
    border-radius: 10px;
}

.shadow {
    box-shadow: 10px 10px 10px rgb(68, 68, 68);
}

.texto {
    padding-left: 15px;
    padding-top: 15px;
    font-family: Arial, Helvetica, sans-serif;
}

.principal-font {
    padding-top: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.secondary-font {
    padding-top: 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}

.base {
    background-color: rgb(11, 36, 251);
}

.important {
    background-color: rgb(1, 8, 100);
}

.warning {
    background-color: rgb(45, 255, 254);
}